.marquee {
  white-space: nowrap;
  overflow: hidden;
}

.marquee-item {
  display: inline-block;
  margin-right: 30px; /* Adjust as needed */
  animation: marquee 15s linear infinite; /* Adjust the duration as needed */
}

@keyframes marquee {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}

.about-me {
  background-color: #333; /* Dark background color */
  color: #fff; /* White text color */
  padding: 20px;
}

.about-me-image {
  text-align: center;
}

.about-me-image img {
  max-width: 100%;
  height: auto;
  border-radius: 50%;
  transition: transform 0.5s; /* Add image loading effect */
}

.about-me-image img.loading {
  animation: loading 2s linear infinite; /* Loading animation */
}

@keyframes loading {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
